import React, { PureComponent } from "react";
import CtaButton from "../../Elements/CtaButton";
import { RATINGS_PATH } from '../../../../constants'
import { TWITTER_LOGO_FN } from "../../../../utils";

export default class CardHover extends PureComponent {
  render() {
    const { item, wall, network , adjustWidth, cardWidth} = this.props;
    const isShopIcon = (item.ugc_products && item.ugc_products.length > 0) ? true : false;
    const nerworkColor = wall.ThemeRule.iconType == 505 ? network.color : wall.ThemeRule.iconColor; const networkIcon = network.icon.replace('fa-', '');
    const styleCta = { fontFamily: wall.ThemeRule.css_font }
    const socialIconCss = cardWidth <= 700 && cardWidth > 500 ? {fontSize: '26px'} : cardWidth <= 500 && cardWidth > 200? {fontSize: '22px'} : cardWidth <= 200 ? {fontSize: '18px'} : {}

    return (
      <div className="ts_cs_social_warpper">
        <div className="ts_cs_social_wrappeer_in">
          <div className="ts_cs_social_" style={{ color: 'rgb(255,255,255)' }}>
            {(item.rating != null && item.rating != 0) ? <div className="ts_cs_social_rating" style={{backgroundImage: `url(${RATINGS_PATH}${item.networkId}/${item.rating}.png)`}} ></div>
              : <div className={`tb__icon tb-${networkIcon}`} style={{ color: nerworkColor, ...socialIconCss }}><div></div></div>
            }
           
          </div>
          <div className="ts_cs_btn_wrapper">
          {adjustWidth > 190 && isShopIcon ? <CtaButton shoppingText={wall.ThemeRule.shoppingText} ctaClass={'ts_cs_button'} styleCta={styleCta} />:''}
          </div>
        </div>
      </div>
    );
  }
}