import React, { PureComponent } from "react";
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, marginTop_mainDiv } from '../../../../utils'

export default class Image extends PureComponent {

  componentDidMount() {
    const resizeObserver = new ResizeObserver((entries) => {
      window.dispatchEvent(new Event('resize'));
      this.getMarginTop();
  })
  resizeObserver.observe(document.querySelector('.taggshopContainer'));

  }
  getMarginTop(){
    setTimeout(() => window.isEditor ?  marginTop_mainDiv(99) :'', 100)
  }


  render() {
    const { ImageClass, ImageUrl, wall, item , width, height, } = this.props

    const Image = {
      backgroundImage:`url(${ImageUrl})`, paddingBottom: `100%`, width: '100%'
    };
    const styleImg = { height: item.hotspot ? `100%` : `auto`, width: item.hotspot ? `auto` : `100%`, margin: `0px auto` }

  
    return (
      <div className={ImageClass} style={styleImg}>
         <img loading="lazy"  data-src={ImageUrl}   className={`ts_cs_img__ imgID${item.id}`} src={ImageUrl}  data-is-optmized="0"   data-link={item.link} data-load="0" data-wall-id={wall.Wall.id} data-item-id={item.id} onLoad={this.onLoadImage} data-filter-id={item.filterId} data-stories={item.stories} data-network={item.networkId}  height={height} width={width} style={styleImg} onError={(e) => { if(item.networkId !== `28`) IMG_LOAD_AFTER_COMPLETE_LAYOUT(e) }} alt=""/>

      </div>
    );
  }
}